.title {
  /* position: relative; */
  z-index: 2;
  color: #6EFCBB;
  font-weight: bold;
  font-size: 42pt;
  font-family: sans-serif;
  text-shadow: 4px 4px 0px #4AB18E;
  line-height: 120px;
  margin-bottom: 50px
}
.card {
    border: 4px rgb(187, 115, 20) solid;
    border-radius: 2px;
    height: 200px;
    width: 200px;
    margin: 1rem;
    position: relative;
    box-shadow: 4px 4px 10px rgb(56, 56, 56);
    transition: all .2s ease-in-out;
  }
  
  .card > .img-container {
    height: 100%;
    width: 100%;
    overflow: hidden;
    background:rgb(255, 255, 255);
  }
  .card:hover {
    box-shadow: 8px 8px 20px rgb(56, 56, 56);
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
  }
  
  .card:active {
    -webkit-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
  }
body,html {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
